:root {
  --bg: #0b2540;
  --bg-alt: #0e2d4d;
  --panel: #0f3560;
  --text: #e9f0f7;
  --muted: #b8c7d9;
  --brand: #2bb0ed;
  --brand-strong: #0ea5e9;
  --accent: #7c3aed;
  --highlight: #f4d03f;
  --shadow: 0 8px 30px #00000040;
  --black: #000;
  --white: #fff;
}

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  color: var(--black);
  margin: 0;
  font-family: Poppins, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.6;
}

p, li {
  color: #626262;
  font-size: 15px;
  line-height: 2;
}

.container {
  width: min(1120px, 92%);
  margin: 0 auto;
}

.site-header {
  z-index: 999;
  backdrop-filter: saturate(160%) blur(10px);
  background: #272728cc;
  border-bottom: 1px solid #ffffff0f;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.header-inner {
  justify-content: space-between;
  align-items: center;
  margin: 0 40px;
  display: flex;
}

.brand {
  color: var(--text);
  align-items: center;
  gap: 10px;
  font-weight: 800;
  text-decoration: none;
  display: inline-flex;
}

.brand-mark {
  background: linear-gradient(135deg, var(--brand) 0%, var(--accent) 100%);
  width: 28px;
  height: 28px;
  box-shadow: var(--shadow);
  border-radius: 6px;
  place-items: center;
  display: inline-grid;
}

.brand-text {
  letter-spacing: .4px;
}

.site-nav > ul {
  align-items: center;
  gap: 18px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.site-nav > ul > li > a {
  color: var(--white);
  border-radius: 8px;
  padding: 8px 10px;
  font-size: 20px;
  font-weight: 600;
  text-decoration: none;
  position: relative;
}

.services-dropdown {
  position: relative;
}

.dropdown-menu {
  backdrop-filter: saturate(160%) blur(10px);
  min-width: 250px;
  box-shadow: var(--shadow);
  z-index: 100;
  text-align: center;
  background: #272728f2;
  border-radius: 8px;
  padding: 8px 0;
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 8px !important;
}

.dropdown-menu li {
  border-bottom: 1px solid #fff3;
  margin: 0;
  padding: 0;
  display: block;
}

.dropdown-menu li:last-child {
  border-bottom: none;
}

.dropdown-menu a {
  color: var(--muted);
  border-radius: 0;
  padding: 8px 16px;
  font-size: .95em;
  transition: all .2s;
  display: block;
}

.dropdown-menu a:hover {
  color: var(--text);
  background: #ffffff1a;
}

.services-dropdown:hover .dropdown-menu {
  text-decoration: none;
  display: block;
}

.site-nav a:hover, .site-nav a:focus {
  color: var(--text);
  background: #ffffff0f;
}

.site-nav a:after {
  content: "";
  background: var(--highlight);
  transform-origin: 0;
  border-radius: 3px;
  height: 3px;
  transition: transform .2s ease-in-out;
  position: absolute;
  bottom: -10px;
  left: 8px;
  right: 8px;
  transform: scaleX(0);
}

.site-nav a:hover:after, .site-nav a:focus:after, .site-nav a.active:after {
  transform: scaleX(1);
}

.nav-toggle {
  background: none;
  border: 0;
  border-radius: 8px;
  padding: 6px;
  display: none;
}

.nav-toggle:focus-visible {
  outline: 2px solid var(--brand);
}

.nav-toggle-bar {
  background: var(--text);
  width: 24px;
  height: 2px;
  margin: 5px 0;
  transition: transform .2s;
  display: block;
}

.hero {
  padding-top: 84px;
  position: relative;
}

.hero-inner {
  grid-template-columns: 1.2fr .8fr;
  align-items: center;
  gap: 32px;
